function ScurtIstoric() {
   const smiles = ['(≥o≤)', '(╥﹏╥)', '(._.`)', '(≖_≖)'];
   const randNum = Math.floor(Math.random() * (4 - 0) + 0);
   const smile = smiles[randNum];
   return (
      <main className="main">
         <section className="error">
            <p className="error__title">{smile}</p>
            <p className="error__text">Din păcate, această pagină nu există.</p>
         </section>
      </main>
   );
}
export default ScurtIstoric;
